:root {
    --sr-white: #fff;
    --sr-light-gray: #f4f5f9;
    --sr-black: #000;
    --sr-primary: #5467ff;
    --sr-secondary: #01d7cf;
    --sr-danger: #f25601;
    --sr-yellow: #f4c703;
    --sr-shadow-color: rgba(0, 0, 0, 0.1);
    --sr-text-color: #252525;
}
* {
    box-sizing: border-box;
    margin: 0;
    outline: none;
    padding: 0;
}
body {
    background-color: #f4f5f9;
    background-color: var(--sr-light-gray);
    color: #252525;
    color: var(--sr-text-color);
    font-family: open sans, sans-serif;
    padding: 20px;
}

@media (max-width: 500px) {
    body,
    main,
    .sr-app {
        /* max-width: 100vw; */
        min-height: 100vh;
    }
}

[contenteditable] {
    -webkit-user-select: text;
    user-select: text;
}

/* ol,
ul {
