/* .sr-app {
    height: 800px;
} */

small {
    font-size: 13px;
}
.sr-overlay {
    background: #00000085;
    height: 100vh;
    left: 50%;
    position: fixed;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 100vw;
    z-index: 9999;
}
.sr-container {
    background-color: #fff;
    background-color: var(--sr-white);
    border: 1px solid #fff;
    border: 1px solid var(--sr-white);
    border-radius: 10px;
    box-shadow: 5px 6px 10px rgba(0, 0, 0, 0.1);
    box-shadow: 5px 6px 10px var(--sr-shadow-color);
    padding: 10px;
}
.sr-bg-danger {
    background-color: #f25601 !important;
    background-color: var(--sr-danger) !important;
}
.sr-bg-white {
    background-color: #fff !important;
    background-color: var(--sr-white) !important;
}
.sr-bg-light-gray {
    background-color: #f4f5f9 !important;
    background-color: var(--sr-light-gray) !important;
}
.sr-bg-black {
    background-color: #000 !important;
    background-color: var(--sr-black) !important;
}
.sr-bg-primary {
    background-color: #5467ff !important;
    background-color: var(--sr-primary) !important;
}
.sr-bg-secondary {
    background-color: #01d7cf !important;
    background-color: var(--sr-secondary) !important;
}
.sr-bg-yellow {
    background-color: #f4c703 !important;
    background-color: var(--sr-yellow) !important;
}
.sr-cl-yellow {
    color: #f4c703 !important;
    color: var(--sr-yellow) !important;
}
.sr-cl-danger {
    color: var(--sr-danger) !important;
}
.sr-cl-white {
    color: #fff !important;
    color: var(--sr-white) !important;
}
.sr-cl-light-gray {
    color: #f4f5f9 !important;
    color: var(--sr-light-gray) !important;
}
.sr-cl-black {
    color: #000 !important;
    color: var(--sr-black) !important;
}
.sr-cl-primary {
    color: #5467ff !important;
    color: var(--sr-primary) !important;
}
.sr-cl-secondary {
    color: #01d7cf !important;
    color: var(--sr-secondary) !important;
}
.sr-btn {
    background-color: #f4f5f9;
    background-color: var(--sr-light-gray);
    border: none;
    border-radius: 5px;
    color: #fff;
    color: var(--sr-white);
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    transition: all 0.1s;
}
.sr-btn:hover {
    opacity: 0.85;
}
.sr-btn + .sr-btn {
    margin-left: 10px;
}
.card-container {
    background: #f4f5f9;
    background: var(--sr-light-gray);
    border-radius: 12px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    left: 50%;
    max-height: 95%;
    overflow: auto;
    padding: 20px;
    position: relative;
    position: fixed;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 90%;
    z-index: 9999;
}
.card-container::-webkit-scrollbar {
    display: none;
}
.card-container {
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.card-container .sr-card {
    width: calc(100% / 3 - 25px);
}
@media (max-width: 1000px) {
    .card-container .sr-card {
        width: calc(100% / 2 - 25px);
    }
}
@media (max-width: 500px) {
    .card-container .sr-card {
        width: 100%;
    }

    .card-container {
        width: 100vw;
        height: 100vh;
    }
}
.sr-card {
    --space: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding: var(--space);
    position: relative;
}
.sr-card:after {
    color: #ecececb0;
    content: attr(data-level);
    font-size: 40px;
    font-weight: 700;
    line-height: 40px;
    position: absolute;
    right: calc(var(--space) + 8px);
    top: var(--space);
}
.sr-card-header .spelling,
.type-words {
    color: #f4c703;
    color: var(--sr-yellow);
    font-weight: 700;
}
.type-words {
    color: #f4f5f9;
    color: var(--sr-light-gray);
    color: gray;
    font-weight: 400;
}
.sr-title {
    color: #494949;
    display: inline-block;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 6px;
}

.sr-type-word-container,
.sr-title {
    margin-right: 6px;
}

.sr-type-word-container {
    display: inline-block;
}
.image-container {
    height: 200px;
    margin-bottom: 3px;
    overflow: hidden;
    width: 100%;
}
.image-container img {
    height: 100%;
    object-fit: cover;
    width: 100%;
}
.note {
    color: gray;
    margin-bottom: 4px;
}
.sr-card:hover .translate,
.sr-card:hover .sr-meaning-flag {
    opacity: 1;
    /* display: block; */
}

.sr-card:hover .meaning {
    /* opacity: 0; */
    /* display: none; */
}
.translate {
    opacity: 0;
    display: inline-block;
    transition: opacity 0.8s;
    color: var(--sr-secondary);
}

.sr-meaning-flag {
    opacity: 0;
    transition: opacity 0.8s;
    margin: 0 7px;
}

.meaning {
    color: #808080a3;
    transition: all 0.2s;
}
.sr-card .sr-btn {
    padding: 15px 0;
}
.note.over-time {
    color: #ffe262;
}
.remaining-time {
    font-size: 14px;
}
.example-container {
    /* display: flex; */
    flex-direction: column;
    margin: 10px 0;
    padding: 5px;
    position: relative;
}
.example-container:after {
    background-color: #01d7cf;
    background-color: var(--sr-secondary);
    border-radius: 999px;
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 2px;
}
.sr-example {
    color: gray;
    font-style: italic;
    padding-left: 20px;
    width: 100%;
}
.sr-example + .sr-example {
    margin-top: 7px;
}
.card-actions {
    align-items: flex-end;
    display: flex;
    flex: 1 1;
    justify-content: space-between;
}
.card-actions .sr-btn {
    white-space: nowrap;
    width: 100%;
}
.card-actions .sr-btn.sr-bg-danger {
    display: none;
    transition: display 4s;
}
.sr-card:hover .card-actions .sr-btn.sr-bg-danger {
    display: inline-block;
}
.form-container {
    bottom: 40px;
    display: flex;
    flex-direction: column;
    flex-direction: row;
    justify-content: center;
    left: 50%;
    /* min-width: 800px; */
    position: fixed;
    -webkit-transform: translate(-50%);
    transform: translate(-50%);
    width: 80%;
    z-index: 9999999;

    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

@media (max-width: 700px) {
    .form-container {
        /* max-width: 400px; */
        min-width: unset;
        width: 95%;
        flex-direction: column;
    }

    .sr-form-my-wrapper-more-info {
        flex-wrap: wrap;
    }
}

.form-container .wrap-container .my-wrapper {
    display: flex;
}
.form-container .wrap-container + .wrap-container {
    margin-left: 10px;
}
.wrap-container.full {
    flex: 1 1;
}
.form-container .wrap-container .my-wrapper > .form-input-wrapper {
    margin-left: 10px;
    padding: 5px 0;
    width: 100%;
}
input.sr-input-radio {
    display: none;
}
input.sr-input-radio:checked + label.sr-label-radio {
    background-color: #01d7cf;
    background-color: var(--sr-secondary);
    color: #fff;
    color: var(--sr-white);
}
.sr-radio-group {
    display: inline-block;
}
label.sr-label-radio {
    align-items: center;
    background: #f4f5f9;
    background: var(--sr-light-gray);
    border-radius: 9999px;
    color: #cbcbcb;
    cursor: pointer;
    display: flex;
    font-weight: 700;
    height: 30px;
    justify-content: center;
    margin: 0;
    transition: all 0.3s;
    width: 30px;
}
label.sr-label-radio:hover {
    opacity: 0.8;
}
.sr-radio-group + .sr-radio-group {
    margin-left: 8px;
}
.form-input-wrapper.sr-wrap-radio {
    align-items: center;
    display: flex;
    justify-content: center;
}
@-webkit-keyframes move-up {
    0% {
        -webkit-transform: translate(-50%, 50%);
        transform: translate(-50%, 50%);
    }
    to {
        -webkit-transform: translate(-50%);
        transform: translate(-50%);
    }
}
@keyframes move-up {
    0% {
        -webkit-transform: translate(-50%, 50%);
        transform: translate(-50%, 50%);
    }
    to {
        -webkit-transform: translate(-50%);
        transform: translate(-50%);
    }
}
.row {
    display: flex;
    flex-wrap: wrap;
    margin: 10px 0;
}
.col-6 {
    padding: 0 10px;
    width: 50%;
}
.col-12 {
    padding: 0 10px;
}
.col-12,
.w-100 {
    width: 100%;
}
label {
    display: block;
    margin-bottom: 5px;
}
.form-input-wrapper > input,
.form-input-wrapper > div#examples {
    background: #fff;
    background: var(--sr-white);
    /* color: gray; */
    border: none;
    border-bottom: 3px solid #01d7cf;
    border-bottom: 3px solid var(--sr-secondary);
    font-size: 16px;
    padding: 10px;
    -webkit-transform: all 0.3s;
    transform: all 0.3s;
    width: 100%;
}
.form-input-wrapper > input:focus {
    border-color: #5467ff;
    border-color: var(--sr-primary);
}

div#examples {
    color: gray;
}
.sr-progress-chart {
    align-items: flex-end;
    background-color: #f4f4f4ea;
    bottom: 50px;
    box-shadow: 5px 6px 20px rgba(0, 0, 0, 0.3);
    display: flex;
    height: 350px;
    justify-content: space-between;
    left: 50%;
    max-width: 650px;
    padding: 25px;
    position: fixed;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 100%;
    z-index: 999;
}
.sr-progress-chart .bar {
    transition: height 1s ease-in;
}
.bar {
    -webkit-animation: progress-chart 2s ease-in-out;
    animation: progress-chart 2s ease-in-out;
    background-color: #07c;
    border: 1px solid #fff;
    border-radius: 4px;
    position: relative;
    width: 12%;
}
.bar + .bar {
    margin-left: 15px;
}
.bar:after {
    color: #01d7cf;
    color: var(--sr-secondary);
    content: attr(data-counter);
    top: -25px;
}
.bar:after,
.bar:before {
    left: 50%;
    position: absolute;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}
.bar:before {
    bottom: -25px;
    color: gray;
    content: attr(data-level);
}
.bar-empty {
    background-color: gray;
    border-radius: 999px 999px 5px 5px;
}
.progress-chart-close-btn.close-btn {
    position: absolute;
    right: 10px;
    top: 10px;
}
.close-btn {
    background-color: #e5e5e5;
    border: none;
    border-radius: 999px;
    color: #ccc;
    cursor: pointer;
    height: 20px;
    padding: 18px;
    position: relative;
    transition: all 0.3s ease-in-out;
    width: 20px;
}
.close-btn:hover {
    background-color: #dfdfdf;
    color: #01d7cf;
    color: var(--sr-secondary);
}
.close-btn:hover .close-icon {
    font-size: 24px;
}
.close-icon {
    font-size: 28px;
    left: 50%;
    line-height: 20px;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.card-actions-container {
    bottom: 10px;
    display: flex;
    justify-content: center;
    left: 50%;
    position: fixed;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 99999;
}
.form-word.base-actions-container {
    align-items: center;
    display: flex;
    justify-content: center;
}
.form-word.base-actions-container .action-btn {
    border-radius: 999px;
    display: flex;
    padding: 10px;
    transition: all 0.5s;
}
.form-word.base-actions-container .action-btn + .action-btn {
    margin-left: 10px;
}
.form-word.base-actions-container .action-btn.btn-award {
    --fill-color: #ffe367;
}
.form-word.base-actions-container .action-btn.btn-flash-cards {
    --fill-color: var(--sr-secondary);
}
.form-word.base-actions-container .action-btn svg {
    fill: #cbcbcb;
    transition: all 0.3s;
    width: 20px;
}
.form-word.base-actions-container .action-btn:hover svg {
    fill: var(--fill-color);
}
@-webkit-keyframes progress-chart {
    0% {
        height: 0;
    }
    to {
        height: attr(data-height);
    }
}
@keyframes progress-chart {
    0% {
        height: 0;
    }
    to {
        height: attr(data-height);
    }
}

/* Nav mobile */
.nav-mobile {
    display: none;
}
@media (max-width: 600px) {
    .nav-mobile {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }
    .nav-mobile .sr-btn {
        margin: 5px 0;
    }
}
